.page-header {
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 2*$lg 0; }
  &__logo {
    display: inline-block;
    margin-right: 2*$lg;
    max-width: 180px;
    @include rmax(lg) {
      max-width: 140px; }
    @include rmax(xs) {
      margin-bottom: $lg; } }
  &__tel {
    font-size: 18px;
    text-decoration: none;
    @include rmax(sm) {
      margin-top: $lg; }
    @include r(sm) {
      margin-left: auto; } }
  &__slogan {
    line-height: 1.2;
    margin: 0 $lg 0 0;
    font-size: 18px;
    color: $c-main; }
  &__title {
    display: flex;
    flex-wrap: wrap; } }

