.section {
  padding: 2*$l-g 0;
  @include r(sm) {
    padding: 4*$l-g 0; } }

.bgc-light {
  background-color: #eee; }

.bgc-dark {
  background-color: $c-sec; }

.title {
  font-size: 32px;
  margin: 0 0 .5em;
  color: $c-brand;
  line-height: 1.2;
  @include r(sm) {
    font-size: 36px; } }
