.promo-service {
  &__wrap {
    display: flex;
    flex-direction: column;
    position: relative;
    @include r(sm) {
      flex-direction: row; }
    @include r(lg) {
      justify-content: center;
      align-items: center; } }
  &__img {
    position: absolute;
    z-index: -1;
    top: 0;
    height: 100%;
    @include rmax(lg) {
      right: -250px;
      width: 500px;
      opacity: .5; }
    @include r(lg) {
      left: 50%;
      transform: translateX(-50%); }
    img {
      height: 100%;
      width: 100%;
      object-fit: contain; } }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    font-weight: bold;
    font-size: 16px;
    @include r(lg) {
      width: 50%; }
    @include r(xxl) {
      font-size: 22px;
 } }      // position: absolute
  &__item {
    padding: 2em 0;
    position: relative;
    flex: 0 0 auto;
    line-height: 1.2;
    &:before {
      content: "";
      position: absolute;
      top: 50%;
      width: 2em;
      height: 4em;
      background: url("../../../../assets/img/service-round.svg") no-repeat;
      background-size: cover;
      transform: translate(0, -50%); }
    @include rmax(lg) {
      padding: 1em 0 1em 40px;
      &:before {
        left: 0;
        width: 1.5em;
        height: 3em; } } }
  &__footer {
    margin: 2*$lg 0 4*$lg;
    text-align: center;
    font-size: 22px;
    line-height: 1.2;
    color: $c-main;
    @include r(sm) {
      font-size: 28px; } }
  &__tel {
    font-size: 1.5em; } }

.promo-service__list--left {
  @include r(lg) {
    text-align: right;
    padding-right: 10em;
    .promo-service__item {
      padding-right: 1.5em;
      &:nth-child(2),
      &:nth-child(3) {
        left: -3em; }
      &:before {
        right: 0; } } } }


.promo-service__list--right {
  @include r(sm) {
    margin-left: 2*$lg; }

  @include r(lg) {
    padding-left: 10em;
    .promo-service__item {
      padding-left: 1.5em;
      &:nth-child(2),
      &:nth-child(3) {
        left: 3em; }
      &:before {
        left: 0;
        transform: translate(0, -50%) rotate(180deg); } } } }

